import React, {useContext, useEffect, useRef, useState} from "react";
import styled, { css } from "styled-components";
import { IsDarkContext } from "../context/IsDarkContext";

const Outside = styled.div`
    background-color: ${({theme}) => theme.darkTheme.bg3};
    border-style: solid;
    border-color: ${({theme}) => theme.darkTheme.bd1};
    flex-shrink: 0 !important;
    position: relative;

    ${props => props.theme.verticalMode ? css`
        width: 100%;
        height: ${props=> props.size.width / 2}px;
        padding: 0.5vh;
        margin-bottom: 8vw;
        transition: height 0.25s;
    ` : css`
        position: absolute;
        top: 1.5vw;
        left: 1.5vw;
        width: ${props=>  props.mainsize.height / props.mainsize.width * 100 * 0.43}%;
        height: calc(100% - 3vw);
        padding: 0.5vw;
        transition: width 0.25s;
    `}
    
`;

const SpaceBorder = styled.div`
    border-style: dashed;
    border-color: ${({theme}) => theme.darkTheme.bd1};
    ${props => props.theme.verticalMode ? 'border-left' : 'border-bottom'}: none;
    margin: ${props => props.theme.verticalMode ? '1.5vh' : '1vw'};
`;

const Main = styled.div`
    background-color: ${({theme}) => theme.darkTheme.bg4};
    border-style: solid;
    border-color: ${({theme}) => theme.darkTheme.bd1};
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    ${props => props.theme.verticalMode ? css`
        padding: 1vh;
        justify-content: space-between;
        & > div{
            height: 100%;
        }

        & > ${SpaceBorder}:not(:nth-child(2)){
            display: none;
        }
    ` : css`
        flex-direction: column;
        padding: 0.5vw;
        & > div{
            width: 100%;
        }
    `}
`;

const ProfileImage = styled.div`
    border-style: solid;
    border-color: ${({theme}) => theme.darkTheme.bd1};
    flex-shrink: 0;

    ${props => props.theme.verticalMode ? css`
        width: calc(${props=> props.size.width / 2}px - 3.3vh) !important;
        height: calc(${props=> props.size.width / 2}px - 3.3vh) !important;
    ` : css`
        width: calc(${props=> props.size.width}px - 2vw) !important;
        height: calc(${props=> props.size.width}px - 2vw) !important;
    `}
    
    & > img{
        width: 100%;
        height: 100%;
    }
`;

const Intro = styled.div`
    width: 50%;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    ${props => props.theme.verticalMode ? css`
        font-size: 1.2em;
    ` : css`
        padding-left: 0.5vw;
    `}

    & > div{
        height: ${props => props.theme.verticalMode ? '17%' : '20%'};
        display: flex;
    }

    & > div > img{
        max-height: 70%;
        margin-right: ${props => props.theme.verticalMode ? '1vh' : '0.5vw'};
        border-radius: 0;
    }
`;

const LinkUrl = styled.select`
    background-color: ${({theme}) => theme.darkTheme.bg1};
    height: 7%;
    width: 98%;
    flex-shrink: 0;
    border-radius: 0 !important;
    font-size: 1em;
    ${props => props.theme.verticalMode && 'display: none;'}
`;

const ToggleButton = styled.button`
    width: 5vh;
    height: 5vh;
    position: absolute;
    border: none;
    cursor: pointer;
    background-color: transparent;
    background-size: cover;

    ${props => props.theme.verticalMode ?
        (props.profileClose ? css`
            background-image: url(${process.env.PUBLIC_URL}/images/dArrow${props.theme.darkTheme.dark && 'W'}.png);
        ` : css`
            background-image: url(${process.env.PUBLIC_URL}/images/uArrow${props.theme.darkTheme.dark && 'W'}.png);
        `)
    :

        (props.profileClose ? css`
            background-image: url(${process.env.PUBLIC_URL}/images/rArrow${props.theme.darkTheme.dark && 'W'}.png);
        ` : css`
            background-image: url(${process.env.PUBLIC_URL}/images/lArrow${props.theme.darkTheme.dark && 'W'}.png);
        `)
    }

    ${props => props.theme.verticalMode ? css`
        left: calc(50% - 2.5vh);
        bottom: -2vh;
    ` : css`
        top: calc(50% - 2.5vh);
        right: -2vh;
    `}
`;


function Profile(props) {
    const isDark = useContext(IsDarkContext);

    const selectLink = (e)=>{
        const link = e.target.value;
        if(link){
            window.open(link);
            e.target.selectedIndex = 0;
        }
    }

    const [size, setSize] = useState({ width: 0, height: 0 });
    const mainRef = useRef();

    useEffect(() => {
        const handleResize = () => {
            const mainElement = mainRef.current;
            if (mainElement) {
                setSize({
                    width: mainElement.offsetWidth,
                    height: mainElement.offsetHeight
                });
            }
        };
        
        setTimeout(() => {
            handleResize();
        }, 1);
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, [size]);

    return(
        <Outside ref={mainRef} size={size} mainsize={props.mainsize}>
            <Main>
                <ProfileImage size={size}>
                    <img src={`${process.env.PUBLIC_URL}/images/profile.png`} alt="profile" />
                </ProfileImage>
                <SpaceBorder />
                <Intro>
                    <div>
                        <img src={isDark?`${process.env.PUBLIC_URL}/images/birth2.png`:`${process.env.PUBLIC_URL}/images/birth.png`} alt="birth" />
                        <div>1997. 10. 19.</div>
                    </div>
                    <div>
                        <img src={isDark?`${process.env.PUBLIC_URL}/images/home2.png`:`${process.env.PUBLIC_URL}/images/home.png`} alt="home" />
                        <div> </div>
                    </div>
                    <div>
                        <img src={isDark?`${process.env.PUBLIC_URL}/images/tell2.png`:`${process.env.PUBLIC_URL}/images/tell.png`} alt="tell" />
                        <div>010 - 2431 - 4307</div>
                    </div>
                    <div>
                        <img src={isDark?`${process.env.PUBLIC_URL}/images/email2.png`:`${process.env.PUBLIC_URL}/images/email.png`} alt="email" />
                        <div>sojung017@naver.com</div>
                    </div>
                </Intro>
                <SpaceBorder />
                <LinkUrl
                    onChange={selectLink}
                >
                    <option value=""></option>
                    <option value="https://github.com/KSJ0314">GitHub</option>
                    <option value="https://velog.io/@ksj0314/series">Velog</option>
                    <option value="https://ksj0314.github.io/"></option>
                </ LinkUrl>
            </Main>
            <ToggleButton onClick={props.profileCloseToggle} profileClose={props.profileClose}/>
        </Outside>
    );
}

export default Profile;