import React from "react";
import socials from "../../data/social.json";
import styled from "styled-components";


const Main = styled.div`
    width: 100%;
    height: 100%;
    position: relative;
`;

const Img = styled.div`
    width: 30%;
    height: 55%;
    position: absolute;
    top: 20%;
    left: ${props => props.index*40+15}%;
    cursor: pointer;
    
    & img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        content: ${({ theme, $imgLink, $imgLink2 }) => `url(${!theme.darkTheme.dark ? $imgLink : $imgLink2})`};
    }
    
    & .hover{
        width: 50%;
        height: 50%;
        top: 25%;
        left: 25%;
        opacity: 0;
        transition: 0.2s;
        content: ${({ theme, $imgLink, $imgLink2 }) => `url(${theme.darkTheme.dark ? $imgLink : $imgLink2})`};
    }

    &:hover .hover{
        width: 95%;
        height: 95%;
        left: 2.5%;
        top: 2.5%;
        opacity: 1;
    }
`;

function SocialContain(props){

    const link = (link)=>{
        if(link){
            window.open("about:blank").location.href = link;
        }
    }

    return (
        <Main>
            {socials.map((social, index)=>
                <Img
                    key={index}
                    index={index}
                    onClick={()=>link(social.link)}
                    $imgLink={`${process.env.PUBLIC_URL}${social.img}`}
                    $imgLink2={`${process.env.PUBLIC_URL}${social.img_hover}`}
                >
                    <img alt="social" />
                    <img className="hover" src={`${process.env.PUBLIC_URL}${social.img_hover}`} alt="social" />
                </Img>
            )}
        </Main>
    );
}

export default SocialContain;