import React, { useState } from "react";
import Carousel from 'react-bootstrap/Carousel';
import styled, { css } from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css";

const Main = styled.div`
  width: 90%;
  text-align: center;
  font-size: 1.2rem;
  line-height: 2.5em;

  & .carousel{
    position: static;
  }
  & .carousel-indicators{
    margin: 0;
  }
  & a{
    height: 3rem;
    width: 10%;
  }
  & button{
    background-color: ${props=>props.theme.darkTheme.color} !important;
  }

  & .carousel-control-prev > span{
    background-image: url(${process.env.PUBLIC_URL}/images/lArrow${props=>props.theme.darkTheme.dark && 'W'}.png);
  }
  & .carousel-control-next > span{
    background-image: url(${process.env.PUBLIC_URL}/images/rArrow${props=>props.theme.darkTheme.dark && 'W'}.png);
  }

  ${props => props.theme.verticalMode ? css`
    & .carousel-control-next{
      left: 90%;
    }
    & .carousel-indicators{
      top: 2rem;
    }
    & .index{
      margin-bottom: 0.5rem;
    }
  ` : css`
    & a{
      top: 90%;
    }
    & .carousel-indicators{
      width: 50%;
      bottom: 10%;
    }
    & .carousel-control-next{
      left: 40%;
    }
    & .index{
      width: 50%;
      position: absolute;
      top: 90%;
      left: 0;
    }
  `};

`;

const Img = styled.img`
  max-width: 100%;
  max-height: ${props => props.theme.verticalMode ? '35vh' : '56vh'};
  display: block;
  margin: 0 auto;
`;

function ControlledCarousel(props){
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex) => {
    setIndex(selectedIndex);
  };

  return (
    <Main>
      <div className="index">{index+1}/{props.data.image.length}</div>
      <Carousel activeIndex={index} onSelect={handleSelect}>
        {props.data.image.map((img)=>(
          <Carousel.Item>
            <Img src={`${process.env.PUBLIC_URL}${img}`} />
          </Carousel.Item>
        ))}
      </Carousel>
    </Main>
  );
}

export default ControlledCarousel;