import React, {useRef, useState, useEffect} from "react";
import styled from "styled-components";
import Board from "./Board";
import boards from "../../data/board.json"
// import axios from "axios";

const Scroll = styled.div`
    width: 100%;
    height: 100%;
    border-radius: 0;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
    align-items: center;

    & .err{
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: black;
        color: white;
        font-size: 3em;
        z-index: 3;
        display: flex;
        justify-content: center;
        align-items: center;
    }
`;
const Main = styled.div`
    width: 90%;
    height: 90%;
    border-radius: 0;
`;

const Form = styled.form`
    width: 100%;
    height: 15vw;
    border-radius: 0;
`;

const Title = styled.div`
    border-top-style: solid;
    border-color: #dbdbdb;
	background-color: ${props=>props.theme.darkTheme.bg9};
    border-radius: 0;
    width: 100%;
    height: 2.1vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1vw;

    & input {
        background-color: ${props=>props.theme.darkTheme.bg1};
        
        &[id=id]{
            width: 85%;
            border-radius: 0;
            padding: 0.1vw 0.3vw;
        }

        &[id=pw]{
            width: 11%;
            border-radius: 0;
            padding: 0.1vw 0.3vw;
        }
    }
    & input[readOnly] {
        border: none;
        background-color: transparent;

        &[id=pw]{
            display: none;
        }
    }

    & > div{
        width: 31%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
`;

const GitHub = styled.div`
    width: 11.72%;
    height: 80%;
    cursor: pointer;
    position: relative;

    & img{
        position: absolute;
        border-radius: 0;
    }

    & .default{
        width: 100%;
        height: 100%;
    }

    & .hover{
        width: 50%;
        height: 50%;
        top: 25%;
        left: 25%;
        opacity: 0;
        transition: 0.2s;
    }

    &:hover .hover{
        width: 90%;
        height: 90%;
        left: 5%;
        top: 5%;
        opacity: 1;
    }
`;

const ContentBox = styled.div`
    width: 100%;
    height: 9vw;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    
    & img {
        width: 7vw;
        height: 7vw;
        border-style: dotted;
        border-color: ${({theme}) => theme.darkTheme.bd1};
    }

    & .text{
        width: 80%;
        height: 7vw;
        display: flex;
        flex-direction: column;
        align-items: end;

        & .textarea{
            width: 100%;
            height: 100%;
            flex-shrink: 0;
            border-radius: 0;
            border-style: solid;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        & textarea{
            width: 95%;
            border-radius: 0;
            border: none;
            resize: none;
            background-color: transparent;
        }

        & button{
            margin-top: 1vw;
            padding: 0.2vw 0.5vw;
            border-radius: 0.3vw;
            background-color: ${props=> props.theme.darkTheme.bg1};
            cursor: pointer;
        }
    }
`;

function BoardContain(props){
    const clientId = 'Ov23lic68CDZ1xfKgjdS';
    const redirectUrl = 'https://ksj0314.github.io/my-portfolio/';
    const githubURL = `https://github.com/login/oauth/authorize?client_id=${clientId}&redirect_uri=${redirectUrl}`;
    const githubLogin = () => {
        window.location.href = githubURL;
    };

    const [formData, setFormData] = useState({
        id: '',
        date: '',
        pw: '',
        img: 'default.png',
        content: ''
    });
    const areaRef = useRef();
    const [maxLength, setMaxLength] = useState(197);
    
    useEffect(() => {
        if (areaRef.current) {
            const textarea = areaRef.current;
            textarea.style.height = 'auto';
            textarea.style.height = `${textarea.scrollHeight}px`;
        }
    }, [formData.content]);

    const handleChangeId = (e) => {
        setFormData({
            ...formData,
            id: e.target.value
        });
    };

    const handleChangePw = (e) => {
        setFormData({
            ...formData,
            pw: e.target.value
        });
    };

    const handleChangeContent = (e) => {
        const lines = e.target.value.split('\n');
        setMaxLength(197 - (33*(lines.length-1)));
        if (lines.length <= 6) {
            setFormData({
                ...formData,
                content: e.target.value
            });
        }
    };

    const currentDate = () => {
        const date = new Date();
        const day = date.toLocaleDateString();
        let hours = date.getHours();
        const minutes = date.getMinutes();
        const seconds = date.getSeconds();
        const amPm = hours >= 12 ? '' : '';
        hours %= 12;
        hours = hours || 12; // 0  12 
        const formattedTime = `${day} ${amPm} ${hours}:${minutes < 10 ? '0' : ''}${minutes}:${seconds}`;

        return formattedTime;
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        setFormData(prevFormData => {
            const updatedFormData = {
                ...prevFormData,
                date: currentDate()
            };

            console.log(updatedFormData);
            return updatedFormData;
        });

        /* const updatedData = [boards, formData];
        try {
            await axios.post('../../data/board.json', updatedData); //  POST  
            console.log('Data added successfully!');
        } catch (error) {
            console.error('Error adding data:', error);
        } */
    };

    return (
        <Scroll>
            <div className="err">
                 !
            </div>
            <Main>
                <Form
                    onSubmit={handleSubmit}
                >
                    <Title>
                        <div>
                            <GitHub
                                onClick={()=>githubLogin()}
                            >
                                <img className="default" src={`${process.env.PUBLIC_URL}images/git.png`} alt="git"/>
                                <img className="hover" src={`${process.env.PUBLIC_URL}images/git2.png`} alt="git" title="GitHub "/>
                            </GitHub>
                            <input
                                id="id"
                                type="text"
                                placeholder=""
                                value={formData.id}
                                maxLength={10}
                                onChange={handleChangeId}
                            />
                        </div>
                        <input
                            id="pw"
                            type="password"
                            placeholder=""
                            value={formData.pw}
                            maxLength={8}
                            onChange={handleChangePw}
                        />
                    </Title>
                    <ContentBox>
                        <img src={`./images/board/${formData.img}`} />
                        <div className="text">
                            <div className="textarea">
                                <textarea
                                    ref={areaRef}
                                    type="text"
                                    rows={1}
                                    maxLength={maxLength}
                                    placeholder=" 6, 200."
                                    name="text"
                                    value={formData.content}
                                    onChange={handleChangeContent}
                                />
                            </div>
                            <button type="submit"></button>
                        </div>
                    </ContentBox>
                </Form>
                {boards.map((board)=>(
                    <Board
                        key={board.id}
                        board={board}
                    />
                ))}
            </Main>
        </Scroll>
    );
}

export default BoardContain;