import React, { useEffect, useState } from "react";
import styled, {keyframes} from "styled-components";

const Title = styled.div`
    width: 100%;
    animation-fill-mode: forwards;
    border-radius: 0;
    background-image: url(${props=>props.url});
    background-size: 105% 105%;
    background-position: center;
    position: relative;
`;

const blink = keyframes`
    0%{
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
`;

const on = keyframes`
    100% {
        opacity: 1;
    }
`;

const TextBox = styled.div`
    width: 38%;
    height: 25%;
    background-color: white;
    border: 0.4vw double ${props => props.theme.darkTheme.bg2} !important;
    position: absolute;
    top: 22%;
    left: 54%;
    transform: translate(-50%, -50%);
    white-space: pre-line; /* \n    */
    display: flex;
    justify-content: center;
    align-items: end;
    text-align: center;
    opacity: 0;
    animation: ${on} 0.25s 1s linear forwards;

    & > .whiteBox{
        position: relative;
        width: 80%;
        height: 100%;
        background-color: white;
        z-index: 3;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    & > img{
        width: 10%;
        height: 25%;
        opacity: 0;
        cursor: pointer;
        display: none;
        position: absolute;
        bottom: 0;
        
        &.left{
            left: 0;
        }
        &.right{
            right: 0;
        }
        &.end{
            display: block;
            animation: ${blink} 1s infinite;
        }
    }
    
    & .text{
        color: black !important;
        font-weight: 500;
        font-size: ${props=>props.fullMode?'2.2em':'1.2em'};
        position: relative;
        padding: 0.5vw 0;
    }

    & .text::after{
        content: "|";
        animation: ${blink} 1s infinite;
    }
`;

const Ballon = styled.div`
    width: 40%;
    height: 150%;
    position: absolute;
    top: 60%;
    left: 62%;
    transform: translate(-50%, -50%) rotateY(75deg);
`;
const Ballon2 = styled.div`
    width: 100%;
    height: 100%;
    position: absolute;
    transform: rotateZ(45deg);
    border-right: 1vw double ${props => props.theme.darkTheme.bg2} !important;
    border-bottom: 1vw double ${props => props.theme.darkTheme.bg2} !important;
    background-color: white;
    clip-path: polygon(0% 100%, 100% 100%, 100% 0);
`;

const blinkF = keyframes`
    100% {
        filter: brightness(100%);
    }
`;

const Door = styled.div`
    width: 8%;
    height: 43%;
    position: absolute;
    top: ${props=>props.top}%;
    left: ${props=>props.left}%;
    z-index: 1;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 82%);
    border-radius: 0;

    &:hover{
        cursor: pointer;
    }

    & > img{
        width: 50%;
        position: absolute;
        top: 2%;
        left: ${props=>props.fl}%;
        filter: brightness(50%);
        animation: ${blinkF} 0.2s 2.8s linear forwards;
    }
`;

function About1(props) {
    const text = [
        "  !",
        "   !\n  \n ."
    ];
    const [displayText, setDisplayText] = useState('');
    const [textEnd, setTextEnd] = useState(false);
    const [textIndex, setTextIndex] = useState(0);
    const [firstOpen, setFirstOpen] = useState(true);
    let timeoutId;

    const textChange = () => {
        setTextEnd(false);
        setDisplayText('');
        let currentIndex = 0;
        timeoutId = setTimeout(() => {
            setFirstOpen(false);
            const intervalId = setInterval(() => {
                if (currentIndex < text[textIndex].length) {
                    setDisplayText(prevText => prevText + text[textIndex][currentIndex]);
                    currentIndex++;
                } else {
                    setTimeout(() => {setTextEnd(true);}, 200);
                    clearInterval(intervalId);
                }
            }, 80);
            
            return () => {
                setTextEnd(true);
                clearInterval(intervalId);
            }
        }, firstOpen ? 1200 : 200);
    };
    
    useEffect(() => {
        textChange();
        return () => clearTimeout(timeoutId);
    }, [textIndex]);


    return(
        <>
            <Title
                className={props.class}
                fullMode={props.fullMode}
                url={`${process.env.PUBLIC_URL}/images/cyworld1.png`}
            >
                <TextBox>
                    <img
                        src={`${process.env.PUBLIC_URL}/images/lArrow.png`}
                        className={(textEnd&&textIndex!==0)?'end left':''}
                        onClick={()=>setTextIndex(textIndex-1)}
                    />
                    <div className="whiteBox">
                        <div className="text">
                            {displayText}
                        </div>
                    </div>
                    <img
                        src={`${process.env.PUBLIC_URL}/images/rArrow.png`}
                        className={(textEnd&&textIndex!==text.length-1)?'end right':''}
                        onClick={()=>setTextIndex(textIndex+1)}
                    />
                    <Ballon>
                        <Ballon2/>
                    </Ballon>
                </TextBox>
                <Door
                    onClick={()=>props.aboutChange(-1)}
                    top = "28"
                    left = "73.5"
                    fl = "30"
                >
                    <img src={`${process.env.PUBLIC_URL}/images/downF.png`} alt="downF" />
                </Door>
                <Door
                    onClick={()=>props.aboutChange(1)}
                    top = "39"
                    left = "85.5"
                    fl = "28"
                >
                    <img src={`${process.env.PUBLIC_URL}/images/upF.png`} alt="upF" />
                </Door>
            </Title>
        </>
    );
}

export default About1;